#adv {
  padding-top: 35px;
}

.adv__wrappper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 46px;
}

.adv-item {
  width: 300px;
  border-radius: 10px;
  padding: 38px;
  margin-bottom: 38px;
  background: #f0ae20;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  margin-right: 30px;
}


.adv-item__title {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.02em;
  color: #fff;
  margin-top: 40px;
}

.adv-item__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  margin-top: 46px;
}