#b2b {
 padding-top: 50px;
 padding-bottom: 40px;
}

.b2b-head {
  margin-top: 35px;
  display: flex;
  padding-right: 33px;
}

.b2b-video__wrapper {
  cursor: pointer;
}

.b2b-head__item:last-child {
  margin-left: 30px;
  width: 500px;
}

.b2b__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #525252;
}

.accordion-title {
  background-color: transparent;
  text-align: left;
  border: none;
  margin-left: 12px;
}

.accordion-description {
  font-weight: 500;
  font-size: 15px;
  line-height: 29px;
  color: #525252;
  height: 0;
  overflow: hidden;
  transition: 0.35s;
}

.accordion-description.active {
  height: auto;
  padding: 10px 30px;
  transition: 0.35s;
}

.accardion-btn__wrapper {
  display: flex;
  align-items: center;
}

.accordion-icon {
  width: 50px;
  height: 50px;
  padding: 5px 10px;
}

.accardion-btn__wrapper.active .accordion-icon {
  background: #2d2d2d;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  
}

.accardion-btn__wrapper.active .accordion-icon .accordion-icon__path {
  fill: #fff;
}

.b2b-content {
  display: flex;
  margin-top: 80px;
}

.b2b-slider {
  margin-right: 0 !important;
  width: 750px !important;
  padding-top: 0 !important;
  height: auto !important;
}

.b2b-btn__wrapper {
  width: 100%;
  text-align: center;
}

.b2b-btn {
  padding: 14px 36px;
}
