#consult-form {
  background: url('../images/consult-form-bg.png') no-repeat center top / cover;
  padding: 34px 0 45px;
}

.consult-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.consult-form__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 38px;
  color: #fff;
}

.consult-form__wrapper {
  margin-top: 35px;
}

.consult-form__input {
  padding: 14px;
  width: 240px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #525252;
  border: 1px solid #F2F2F2;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border-radius: 10px !important;
  margin-right: 5px;
}

.consult-form__input::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #525252;
  text-align: center;
}
