#contacts {
  padding-top: 40px;
}

.contacts {
  margin-top: 60px;
}

.contacts-accordion {
  max-width: 500px;
  margin: 0 auto;
}

.contacts-accordion__title {
  background-color: transparent;
  text-align: left;
  border: none;
  width: 100%;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border-radius: 6px;
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: #525252;
  margin-bottom: 10px;
  cursor: pointer;
}

.contacts-accordion__title.active {
  color: #fff;
  background: #f0ae20;
}

.contacts-accordion__title::after {
  content: '';
  display: flex;
  width: 12px;
  height: 12px;
  border: 2px solid;
  border-color: #fff #828282 #828282 #fff;
  transform: rotate(45deg);
  transition: border-color 0.5s ease-in-out, bottom 0.5s ease-in-out;
  transition: 0.2s;
}


.contacts-accordion__title.active::after {
  border-color: #fff #f0ae20 #f0ae20 #fff;
}

.contacts-accordion__description {
  height: 0;
  overflow: hidden;
  transition: 0.35s;
}

.contacts-accordion__description.active {
  height: auto;
  transition: 0.35s;
}

.contacts-accordion__description-item {
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: #797979;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.contacts-accordion__description-item::before {
  content: '';
  width: 50px;
  height: 50px;
  background: url('../images/contacts-icon1.svg') no-repeat center center;
  background-color: #fff;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border-radius: 6px;
  margin-right: 10px  ;
}

.contacts-accordion__description-item:nth-child(2)::before {
  background: url('../images/contacts-icon2.svg') no-repeat center center;
  background-color: #fff;
}

.contacts-accordion__description-item:nth-child(2)::before {
  background: url('../images/contacts-icon3.svg') no-repeat center center;
  background-color: #fff;
}

.staf-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  background: #fff;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border-radius: 10px;
  padding: 10px 5px;
}

.staf-info {
  display: flex;
  align-items: center;
}

.staf-text {
  margin-left: 10px;
}

.staf-name {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #474747;
}

.staf-position {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #474747;
}

.map-wrapper {
  text-align: center;
  margin-top: 20px;
}

.contacts-inform {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #525252;
  margin-top: 80px;
}

hr {
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border: 1px solid #f3f3f3;
  margin-top: 30px;
}