#team {
  background: #fafafa;
  margin-top: 40px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  padding-bottom: 40px;

  .team-title {
    padding-top: 70px;
  }
}

.team-wrapper {
  margin-top: 30px;
  height: 315px;

  .swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .swiper-pagination-bullet {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #fff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
    opacity: 1;
  }
  
  .swiper-pagination-bullet-active {
    width: 24px;
    height: 24px;
    background: #f0ae20;
  }
}

.team-slide {
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 15px 100px 36px 15px;
  margin-right: 40px;
  display: flex;
  color: #525252;
  height: 250px;
}

.team-slide__item:last-child {
  margin-left: 8px;
}

.team-slide__item_title {
  font-weight: 600;
  font-size: 22px;
  line-height: 30px;
}

.team-slide__item_prof {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}

.team-slide__item-social_title {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}

.team-slide__item-social {
  margin-top: 18px;
}

.team-slide__item-social_wrapper {
  display: flex;
}

.team-slide__item-social_item {
  margin-right: 10px;
  margin-top: 7px;
}

.team-slide__item-lang {
  margin-top: 13px;
}

.team-slide__item-lang_title {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}

.team-slide__item-lang_wrapper {
  display: flex;
}

.team-slide__item-lang_item {
  margin-right: 10px;
}

.team-slide.swiper-slide.swiper-slide-active {
  background: #292a2a;
  color: #fff;
}

.ports-map {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.ports-adress__item {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.ports-adress__item:first-child {
  flex-basis: 100%;
}

.ports-adress__item:last-child {
  margin-left: 30px;
}

.posrts-map__info {
  margin-left: 50px;
}

.ports-adress__item-icon {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
}

.ports-adress__item-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #fff;
  margin-left: 10px;
}

.ports-adress__item-text span {
  display: block;
  font-size: 14px;
  line-height: 19px;
}

.ports-adress {
  display: flex;
  flex-wrap: wrap;
}