#ports {
  padding-top: 35px;
  background: url('../images/ports-bg.jpg') no-repeat center top / cover;
  padding-bottom: 110px;
}

.ports-title {
  color: #fff;
}

.pre-tooltip {
  cursor: pointer;
}

.tooltip__wrapper.active {
  .pre-tooltip {
    fill: #f0ae20;
  }

  .pre-tooltip__circle {
    fill: #f0ae20;
  }
}

.tooltip__wrapper:hover {
  .pre-tooltip {
    fill: #f0ae20;
  }

  .pre-tooltip__circle {
    fill: #f0ae20;
  }
}

.tooltip {
  display: none;
}

.posrts-map__info {
  display: none;
}

.posrts-map__info.active {
  display: block;
}

.tooltip.active {
  display: block;
}

.video-wrapper {
  cursor: pointer;
}
