#trust {
  background-color: #fafafa;
  padding-top: 40px;
  position: relative;
  padding-bottom: 40px;

  .swiper-button-next {
    background: #2d2d2d;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-top: -22px;
    position: absolute;
    right: 1%;
    top: 32%;
  }

  .swiper-button-next::after {
    color: #fff;
    font-size: small;
  }
}

.trust-header__slider {
  margin-top: 40px;
  margin-left: -55px;
  width: 100%;
}

.trust-header__slider-wrapper {
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border-radius: 10px;
  background: #fff;
  color: #525252;
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 130px;
  cursor: pointer;
}

.trust-header__slide.active .trust-header__slider-wrapper {
  background: #f0ae20;
  color: #fff;
}

.trust-content__item {
  margin-top: 55px;
  display: none;
}

.trust-content__item.active {
  display: flex;
}

.trust-content__item-left {
  flex-basis: 100%;
  position: relative;
}

.trust-content__item-right {
  margin-left: 80px;
  margin-top: 30px;
}

.trust-person {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #f0ae20;
  position: absolute;
  right: 15%;
  top: 0;
}

.trust-person__img {
  position: absolute;
  left: 10px;
  top: 10px;
}

.trust-btn__wrapper {
  width: 100%;
  display: flex;
  margin-top: 60px;
}

.trust-btn {
  margin: 0 auto;
  background: #2d2d2d;
  width: 240px;
  padding: 12px 0;
}