#usa-collection {
  padding-top: 40px;
  padding-bottom: 200px;
}

.usa-collection__wrapper {
  margin-top: 50px;
  display: flex;
  width: 1700px;
}

.usa-collection__tabs-wrapper {
  width: 385px;
}

.usa-collection__tab-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.usa-collection__tab-header_item {
  cursor: pointer;
  padding: 22px;
  width: 76px;
  height: 76px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.usa-collection__tab-header_item.active {
  background: #2d2d2d;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.usa-collection__tab-header_item.active svg .usa-collection__tab-header_icon {
  fill: #fff;
}

.usa-collection__tabs {
  margin-top: 35px;
}

.usa-collection__item {
  height: 360px;
}

.usa-collection__tab-content {
  display: none;
}

.usa-collection__tab-content.active {
  display: block;
}

.use-collection__tab-title {
  font-weight: 700;
  font-size: 22px;
  line-height: 30px;
  color: #525252;
}

.use-collection__tab-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #525252;
  margin-top: 20px;
}

.use-collection__slider {
  margin-left: 60px;
  width: 980px;
  position: absolute;
  right: 0;
}

.use-collection__slide {
  padding: 10px;
  background: #2d2d2d;
  border-radius: 10px;
  color: #fff;
  width: 305px !important;
}

.use-collection__slide-title {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  margin-top: 8px;
}

.use-collection__slide-title span {
  font-weight: 400;
  font-size: 23px;
}

.car-brand {
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  display: flex;
  align-items: center;
  margin-top: 14px;
}

.car-brand::before {
  content: '';
  display: flex;
  width: 37px;
  height: 14px;
  background: url('../images/ford.svg') no-repeat;
  margin-right: 5px;
}

.use-collection__slide-features {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}

.slide-features__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}

.slide-features__item img {
  width: 30px;
  height: 30px;
  margin-bottom: 10px;
}

.card-btn__wrapper {
  display: flex;
  justify-content: center;
}

.card-btn {
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  background-color: #f0ae20;
  width: 200px;
  border: none;
  border-radius: 100px;
  margin-top: 25px;
  padding: 10px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
  margin-bottom: 10px;
}

.card-btn span {
  font-size: 23px;
  margin-left: 10px;
}

.use-collection__slide-img {
  position: relative;
}

.usa-collection__img-note {
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 3px 7px;
  background: #f0ae20;
  border-radius: 4px
}