#header {
  width: 100%;
  position: absolute;
  margin: 0 auto;
  z-index: 99;
  padding-top: 14px;
}

.header-top {
  display: flex;
  align-items: center;
}

.header-contacts {
  margin-left: auto;
}

.header-contacts {
  display: flex;
  align-items: center;
}

.header-location {
  display: flex;
  align-items: center;
}

.header-location::before {
  content: '';
  width: 23px;
  height: 23px;
  display: flex;
  background: url('../images/loc.svg') no-repeat;
  margin-right: 13px;
}

.header-link {
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  color: #fff;
  text-decoration: none;
  padding: 13px;
}

.header-phone {
  display: flex;
  align-items: center;
}

.header-phone::before {
  content: '';
  width: 1px;
  height: 40px;
  display: flex;
  background-color: #fff;
  margin-right: 36px;
  margin-left: 27px;
}

.header-bottom {
  width: 100%;
  background: #fff;
  border-radius: 10px;
  margin-top: 30px;
}

.navbar {
  display: flex;
  align-items: center;
  padding: 10px;
}

.navbar-item {
  padding: 5px 57px;
  font-weight: 500;
  font-size: 15px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  color: #474747;
  display: flex;
  align-items: center;
  border-right: 1px solid #ececec;
}

.navbar-item:last-child {
  border-right: none;
}