.popup {
  position: fixed;
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bid-popup {
  display: none;
}

.bid-popup__wrapper {
  width: 100%;
  height: 100%;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bid-form {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(30px);
}

.form-cross {
  height: 32px;
  width: 32px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  z-index: 2;
  right: -30px;
  top: -30px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  span {
    width: 12px;
    height: 2px;
    background: #878787;
    display: block;
    position: absolute;
  }

  span:nth-child(1) {
    transform: rotate(45deg);
  }

  span:nth-child(2) {
    transform: rotate(-45deg);
  }
}