html {
  height: 100%; // Footer at bottom
  font-size: 62.5%;
  overflow-x: hidden;
}

body {
  display: flex; // Footer at bottom
  flex-direction: column; // Footer at bottom
  height: 100%; // Footer at bottom
  color: #000;
  font-size: 1.4rem;
  line-height: 1.6rem;
  background: #fff;
  font-family: 'Avenir Next Cyr', sans-serif;
}

.header {
  flex-shrink: 0; // Footer at bottom
}

.main {
  position: relative;
  flex: 1 0 auto; // Footer at bottom
}

.footer {
  z-index: 10;
  flex-shrink: 0; // Footer at bottom
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: none;

  table & {
    max-width: none;
  }
}

.container {
  margin: 0 auto;
  max-width: 1305px;
}

.title {
  font-weight: 500;
  font-size: 36px;
  line-height: 49px;
  text-align: center;
  color: #474747;
}

.title::after {
  content: '';
  display: block;
  width: 40px;
  height: 2px;
  border-radius: 2px;
  background: #f0ae20;
  text-align: center;
  margin: 0 auto;
  margin-top: 5px;
}

.subtitle {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #525252;
  width: 520px;
  margin: 0 auto;
  margin-top: 17px;
}

.btn {
  background: #f0ae20;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border-radius: 100px;
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: #fff;
  border: none;
  padding: 16px 63px;
  width: 200px;
  margin-top: 20px;
}

.main-form {
  width: 414px;
  padding: 30px 20px;
  background: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(30px);
  border-radius: 10px;
}
