#work-process {
  padding-top: 50px;

  .container {
    max-width: 100%;
  }
}

.work-process {
  padding-bottom: 40px;
}

.work-process__wrapper {
  display: flex;
  margin-top: 36px;
  justify-content: flex-end;
}

.work-process__item {
	padding-top: 50px;
	padding-bottom: 50px;
}

.work-process__item:first-child {
  width: 45%;
  margin-left: 0;
  height: 599px;
  overflow: hidden;
}

.work-process__item:last-child {
  width: 590px;
  padding-top: 50px;
	padding-bottom: 50px;
  margin-right: 290px;
}

.work-process__slide img {
	background-position: center;
	background-size: cover;
  border-radius: 10px;
  height: 500px;
}

.work-process__item-title {
  font-weight: 700;
  font-size: 22px;
  line-height: 30px;
  color: #525252;
  display: flex;
  align-items: center;
}

.work-process__item-title_wrapper {
  display: flex;
}

.work-process__item-title_before {
  width: 67px;
  height: 67px;
  display: flex;
  background-color: #f0ae20;
  border-radius: 50%;
  margin-right: 20px;
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.work-process__item-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #525252;
  margin-top: 20px;
}

.work-proccess__slider-main {
  height: 700px;
}

.work-proccess__slider-main .swiper-pagination {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
} 

.work-proccess__slider-main .swiper-pagination-bullet {
  width: 40px;
  height: 40px;
  font-weight: 500;
  font-size: 20px;
  line-height: 27px;
  color: #8f8f8f;
  background: #fff;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 50px !important;
  position: relative;
  border: 3px solid #fff;
}

.work-proccess__slider-main .swiper-pagination-bullet::after {
  content: '';
  display: flex;
  height: 2px;
  width: 40px;
  background-color: #ebebeb;
  margin-left: 50px;
  position: absolute;
  left: -15%;
}

.work-proccess__slider-main .swiper-pagination-bullet:last-child::after {
  display: none;
}

.work-proccess__slider-main .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #f0ae20;
  color: #fff;
  outline: 2px solid #f0ae20;
}
