#first-screen {
  height: 100vh;
  position: relative;
}

.first-screen {
  position: relative;

  .swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
  }
  
  .swiper-pagination-bullet {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #fff;
    opacity: 1;
  }
  
  .swiper-pagination-bullet-active {
    width: 24px;
    height: 24px;
    background: #f0ae20;
  }
}

.first-screen__slider {
  background: url('../images/main-slider1.png') no-repeat center -153px / cover;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
}

.first-screen__slider:last-child {
  background: url('../images/main-slider2.png') no-repeat center -153px / cover;
  height: 100vh;
  width: 100%;
}

.first-screen__title {
  font-weight: 900;
  font-size: 70px;
  line-height: 76px;
  letter-spacing: 4px;
  color: #FFFFFF;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  position: relative;
  margin-left: 20%;
}

.first-screen__title span {
  color: #f0ae20;
  margin-left: 100px;
}

.title-number {
  display: inline;
  font-size: 110px;
  color: #f0ae20;
  position: absolute;
}

.title-currency {
  display: inline;
  font-size: 140px;
  color: #f0ae20;
  position: absolute;
  transform: rotate(-25deg);
}

.first-screen__form {
  position: absolute;
  top: 31%;
  z-index: 9;
  right: 10%;
}

.form-title {
  font-weight: 700;
  font-size: 28px;
  line-height: 38px;
  color: #fff;
}

.form-block {
  display: flex;
  flex-direction: column;
  margin-top: 36px;
  align-items: center;
}

.form-block__input {
  background: #FFFFFF;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border-radius: 6px !important;
  border: none;
  padding: 16px 20px;
  margin-bottom: 10px;
  width: 100%;
}

.form-block__input::placeholder {
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: #9a9a9a;
}
