#free {
  background: url('../images/free-bg.png') no-repeat center top / cover;
  margin-top: 40px;
  padding-bottom: 60px;
}

.free-title {
  font-weight: 600;
  font-size: 30px;
  line-height: 41px;
  text-align: center;
  color: #fff;
  padding-top: 35px;
}

.free {
  display: flex;
  align-items: center;
  justify-content: center;
}

.free-item__title {
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  color: #fff;
}

.free-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 35px;
  padding: 0 20px;
}

.free-btn {
  font-weight: 500;
  font-size: 26px;
  line-height: 36px;
  padding: 12px 41px;
  width: 350px;
}

.free-btn__transparent {
  background: transparent;
  border: 1px solid #f0ae20;
}