#calculate {
  background: url('../images/calc-bg.png') no-repeat center -15px / cover;
  height: 500px;
}

.calculate {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 100px;
}

.calculate-form {
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(30px);
  width: 550px;
}

.calculate-screen__form-title {
  color: #2d2d2d !important;
}

.calculate-text {
  font-weight: 700;
  font-size: 28px;
  line-height: 38px;
  color: #2d2d2d;
  margin-bottom: 40px;
}

.calculate-text:first-child {
  margin-left: -125px;
}

.calculate-text span {
  color: #fff;
}

.calculate-text span span {
  text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.25);
  font-weight: 800;
  font-size: 80px;
  line-height: 38px;
}