#partners {
  padding-top: 35px;
  padding-bottom: 50px;
}

.partners-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 35px;
}

.partners__item {
  width: 285px;
  height: 145px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.17);
  border-radius: 10px;
  margin-bottom: 20px;
}

.partners__item:hover {
  background: #f0ae20;
}