#benefits {
  padding-top: 50px;
  background: #fafafa;
  padding-bottom: 70px;
}

.benefits-tab_wrapper {
  margin-top: 30px;
}

.benefits-tab__header {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.benefits-tab__header-item {
  height: 90px;
  width: 180px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.benefits-tab__header-item_text {
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: #525252;
  margin-top: 11px;
}

.benefits-tab__header-item.active {
  background: #2d2d2d;

  .benefits-tab__header-item_text {
    color: #fff;
  }

  svg .active-tab__img {
    fill: #fff;
  }
}

.benefits-tab__content-item {
  display: flex;
  margin-top: 30px;
  display: none;
}

.benefits-tab__content-item.active {
  display: flex;
}

.benefits-tab__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 25px;
  color: #525252;
  margin-bottom: 20px;
  margin-top: 30px;
}

.benefits-item-tab_text {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #525252;
  margin-bottom: 15px;
}

.benefits-tab__content-item_block {
  flex-basis: 50%;
}

.benefits-tab__content-item_text {
  margin-left: 50px;
  position: relative;
}

.benefits-tab__btn {
  width: 540px;
  border-radius: 10px;
  position: absolute;
  bottom: 0;
}


