#footer {
  background: url('../images/footer-bg.png') no-repeat center right / cover;
  color: #fff;
  padding-bottom: 160px;
}

.footer {
  display: flex;
  padding-top: 40px;
}

.footer__item {
  flex-basis: 45%;
}

.footer__item:last-child {
  flex-basis: 25%;
  margin-left: 150px;
}

.footer__item-text {
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  color: #fff;
}

.footer-social {
  display: flex;
  justify-content: space-between;
}

.footer-menu {
  display: flex;
  justify-content: space-between;
  margin-top: 45px;
}

.footer-menu__item {
  display: flex;
  flex-direction: column;
}

.footer-menu__link {
  font-weight: 500;
  font-size: 17px;
  line-height: 30px;
  color: #fff;
  text-decoration: none;
}

.widgets {
  position: fixed;
  z-index: 9999;
  bottom: 30px;
  display: flex;
  justify-content: space-between;
  width: 1305px;
}

.call {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: url('../images/phone-footer.svg') no-repeat center 15px;
  background-color: #6aaf50;
  cursor: pointer;
  display: flex;
}

.arrow {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: url('../images/arrows.svg') no-repeat center center;
  background-color: #f1b636;
  cursor: pointer;
  display: flex;
}