#assist {
  background: url('../images/assist-bg.png') no-repeat center top / cover;
  height: 800px;
}

.assist {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  padding-top: 85px;
}

.assist-title {
  font-weight: 500;
  font-size: 30px;
  line-height: 41px;
  text-align: center;
  color: #474747;
  width: 550px;
}

.assist-list__wrapper {
  background: url('../images/bg-assist.png') no-repeat center top;
  width: 550px;
  margin-top: 35px;
}

.assist-list {
  padding: 15px 45px;
}

.assist-list__item {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.assist-list__item::before {
  content: '';
  width: 44px;
  height: 44px;
  background: url('../images/assist1.svg') no-repeat center center;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  margin-right: 18px;
}

.assist-list__item:nth-child(2):before {
  background: url('../images/assist2.svg') no-repeat center center;
  background-color: #fff;
}

.assist-list__item:nth-child(3):before {
  background: url('../images/assist3.svg') no-repeat center center;
  background-color: #fff;
}

.assist-list__item:nth-child(4):before {
  background: url('../images/assist4.svg') no-repeat center center;
  background-color: #fff;
}

.assist-list__item:nth-child(5):before {
  background: url('../images/assist5.svg') no-repeat center center;
  background-color: #fff;
}

.assist-form {
  width: 450px;
  margin-right: 45px;
}

.assist-form__btn {
  background: #2d2d2d;
  color: #fff;
}