#secrets {
  padding-top: 45px;
}

.secrets {
  display: flex;
  margin-top: 45px;
}

.secrets-item {
  flex-basis: 50%;
}

.secrets-item:first-child {
  margin-right: 40px;
}

.secrets-header__tab {
  display: flex;
  justify-content: space-between;
}

.secrets-content__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  color: #525252;
  margin-top: 40px;
}

.secrets-content__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 29px;
  color: #525252;
  margin-top: 15px;
}

.secrets-header__tab-item {
  cursor: pointer;
}

.secrets-content__item {
  display: none;
}

.secrets-content__item.active {
  display: block;
}

.secrets-header__tab-item.active svg .secrets-icon {
  fill: #414545;
}

.secrets-header__tab-item.active svg .secrets-circle {
  fill: rgb(240, 174, 32);
}

.secrets-btn__wrapper {
  width: 100%;
  text-align: center;
  margin-top: 40px;
}

.secrets-form__btn {
  padding: 14px 28px;
}